body {
	margin-top: 5em;
}

.modal-background {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 100;
	background-color: rgba(0,0,0, 0.7);
}

.close {
	span {
		position: relative;
		z-index: -1;
	}
}

.fa-heart-o, .fa-heart {
	color: #cf2323;
	cursor: pointer;
}